.companions-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.companions-list__item {
  position: relative;
  width: 270px;
  margin: 0 auto;
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 20px;
  }

  &:hover {
    box-shadow: 0 0 40px rgba(0,0,0,0.2);
  }
}

.companions-list__image-block {
  margin: 0;
  width: 270px;
  height: 270px;
  overflow: hidden;
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
}

.companions-list__image {
  transition: all 1s ease-out;

  &:hover {
   transform: scale(1.1)
  }
}

.companions-list__person-level {
  position: absolute;
  left: 17px;
  top: 16px;
}

.companions-list__country-flags {
  position: absolute;

  top: 258px;
  left: 0;

  width: 100%;
}

.companions-list__person-container {
  background-color: #ffffff;

  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;

  padding-top: 27px;
  padding-bottom: 22px;

  margin-top: -10px;
}

.companions-list__name {
  display: block;
  font-size: 21px;
  line-height: 21px;
  font-weight: bold;
  color: #1d2e5b;

  margin: 0;
  margin-bottom: 10px;

  text-align: center;

  &:hover {
    color: rgba(29, 46, 91, 0.7);
  }

  &:active {
    color: rgba(29, 46, 91, 0.3);
  }
}

.companions-list__tags {
  display: block;
  font-size: 16px;
  line-height: 18px;
  color: #444444;

  text-align: center;

  margin-bottom: 12px;
}

@media (min-width: @tablet-width) {
  .companions-list {
    display: flex;
    justify-content: space-between;
    width: 618px;

    margin-bottom: 80px;
  }

  .companions-list__item {
    margin: 0;
  }

  .companions-list__image{
    width: 100%;
  }

  .companions-list__person-container {
    padding-top: 31px;
    padding-bottom: 29px;
  }

  .companions-list__name {
    margin-bottom: 16px;
  }

  .companions-list__tags {
    margin-bottom: 21px;
  }
}

