.add-plans {
  padding-top: 30px;
}

.add-plans__container {
  width: 270px;
  margin: 0 auto;
}

.add-plans__title-container {
  display: flex;
  justify-content: space-between;

  margin-bottom: 30px;
}

.add-plans__title {
  .form-plan-title;
  margin: 0;
}

.add-plans__link-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  width: 48px;
}

.add-plans__link-slider {
  width:6px;
  height: 6px;

  background-color: #bbbfcc;
  border-radius: 50%;

  &--current {
    background-color: #1d2e5b;
  }
}

.add-plans__step-one,
.add-plans__step-two {
  margin-bottom: 10px;
}

@media (min-width: @tablet-width) {
  .add-plans {
    padding-top: 51px;
  }

  .add-plans__title-container {
    margin-bottom: 53px;
  }

  .add-plans__container {
    width: 618px;
  }

  .add-plans__title {
    font-size: 36px;
    line-height: 50px;
  }

  .add-plans__link-container {
    width: 74px;
  }

  .add-plans__step-one,
  .add-plans__step-two {
    margin-bottom: 50px;
  }
}

@media (min-width: @desktop-width) {
  .add-plans__container {
    width: 1320px;
  }

  .add-plans__step-one,
  .add-plans__step-two {
    margin-bottom: 44px;
  }
}
