.profile-tags {
  margin-bottom: 28px;
  padding-bottom: 20px;
  text-align: center;
  border: 1px solid #cbced9;
  border-radius: 10px;
}

.profile-tags__container {
  margin-top: -8px;
}

.profile-tags__title {
  display: inline-block;
  margin-bottom: 8px;
  padding-left: 17px;
  padding-right: 17px;
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
  text-transform: uppercase;
  color: #1d2e5b;
  background-color: #edeff6;
}

.profile-tags__tags {
  display: inline-block;
  text-align: left;
  width: 212px;
}

@media (min-width: @tablet-width) {
  .profile-tags {
    width: 618px;
    margin-bottom: 0;
  }

  .profile-tags__container {
    display: flex;
    flex-direction: column;
    margin: 0;
    margin-bottom: -25px;
  }

  .profile-tags__title {
    position: relative;
    order: 2;
    width: 100px;
    margin: 0 auto;
  }

  .profile-tags__tags {
    margin-bottom: 29px;
    padding-left: 40px;
    padding-top: 31px;
    width: 500px;
  }
}

@media (min-width: @desktop-width) {
  .profile-tags {
    margin-bottom: 20px;
  }

  .profile-tags__tags {
    margin-bottom: 9px;
  }
}
