.country-flags__list {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.country-flags__item {
  margin-right: 19px;
  width: 35px;
  height: 24px;

  &:last-child {
    margin-right: 0;
  }
}

.country-flags__svg {
  width: 35px;
  height: 24px;
}

.country-flags__tooltip {
  visibility: hidden;

  position: absolute;
  z-index: 1;

  top: 35px;

  font-size: 20px;
  line-height: 24px;
  text-align: center;

  background-color: #101d41;
  color: #ffffff;
  border-radius: 5px;

  padding: 19px 28px 18px 24px;

  &--srilanka {
    left: 8px;
  }

  &--thailand {
    left: 75px;
  }

  &--seychelles {
    left: 125px;
  }

  &--belgium {
    left: 45px;
  }

  &--czech {
    left: 110px;
  }
}

.country-flags__tooltip:after {
  position: absolute;

  content: "";

  top: -16px;
  left: 68px;
  border: 8px solid transparent;
  border-bottom: 8px solid #101d41;
}

.country-flags__tooltip--srilanka:after {
  top: -16px;
  left: 68px;
}

.country-flags__tooltip--thailand:after {
  top: -16px;
  left: 53px;
}

.country-flags__tooltip--seychelles:after {
  top: -16px;
  left: 56px;
}

.country-flags__tooltip--belgium:after {
  top: -16px;
  left: 56px;
}

.country-flags__tooltip--czech:after {
  top: -16px;
  left: 44px;
}

.country-flags__item:hover .country-flags__tooltip {
  visibility: visible;
}
