.filter-show {
  background-color: #e2e5f2;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.filter-show__container {
  width: 270px;
  margin: 0 auto;
}

.filter-show__table {
  width: 220px;
  margin: 0 auto;
  margin-bottom: 26px;
  border-collapse: collapse;
  text-align: center;
  background-color: #ffffff;
}

.filter-show__letter {
  width: 43px;
  height: 40px;
  border: 1px solid #cbced9;
  font-size: 14px;
  line-height: 18px;
  color: #1d2e5b;
  font-weight: 500;

  &--active {
    color: #ffffff;
    background-color: #192144;
    border: 1px solid #192144;
  }
}

.filter-show__list {
  margin: 0;
  margin-left: 25px;
  margin-bottom: 24px;
  padding: 0;
  list-style: none;
}

.filter-show__item {
  font-size: 16px;
  line-height: 22px;
  color: #444444;
}

.filter-show__button {
  position: relative;
  margin-bottom: 27px;
  padding: 0;
  padding-top: 5px;
  padding-bottom: 5px;
  width: 100%;
  border: none;
  border-radius: 30px;
  font-size: 16px;
  line-height: 29px;
  text-transform: uppercase;
  font-weight: bold;
  color: #161c35;
  background-color: #d6daed;
  cursor: pointer;

  &:hover {
    background-color: #c8cee8;
  }

  &:focus {
    color: rgba(29, 46, 91, 0.3);
  }

  .filter-show__svg {
    position: absolute;
    content: "";
    top: 11px;
    left: 75px;
    width: 12px;
    height: 12px;
    fill: #192144;
  }
}

@media (min-width: @tablet-width) {
  .filter-show {
    margin-top: -20px;
    padding-top: 20px;
    border-bottom-left-radius: 40px;
    border-bottom-right-radius: 40px;
  }

  .filter-show__container {
    width: 618px;
  }

  .filter-show__wrapper {
    display: flex;
    margin-bottom: 76px;
  }

  .filter-show__table {
    margin: 0;
    margin-right: 100px;
    width: 400px;
    background-color: transparent;
    text-align: left;
  }

  .filter-show__letter {
    width: 75px;
    height: 80px;
    font-weight: 700;
    color: rgba(25, 33, 68, 0.2);

    &--active {
      background-color: transparent;
      color: #192144;
    }
  }

  .filter-show__letter {
    font-size: 60px;
    border: none;
  }

  .filter-show__list {
    margin: 0;
  }

  .filter-show__item {
    font-size: 20px;
    line-height: 30px;
  }

  .filter-show__button {
    position: relative;
    font-size: 20px;
    padding-top: 40px;
    padding-bottom: 40px;
    margin-bottom: 74px;
  }

  .filter-show__svg {
    top: 50px;
    left: 150px;
    width: 18px;
    height: 18px;
  }
}

@media (min-width: @desktop-width) {

  .filter-show__container {
    width: 1200px;
  }

  .filter-show__wrapper {
    display: none;
  }
}
