.social__list {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffd74b;
  width: 45px;
  height: 45px;
  border-radius: 50%;

  fill: #25345a;

  &:hover {
    background-color: #ffbd30;
  }

  &:focus {
    fill: rgba(187, 146, 61, 0.3);
  }
}

.social__link--index {
  &:hover {
    background-color: #192144;
    fill: #ffffff;
  }

  &:focus {
    fill: #5a5f79;
  }
}
