.companions {
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;

  position: relative;
  z-index: 5;

  padding-top: 100px;
  padding-bottom: 30px;
  margin-top: -30px;

  background-color: #192144;
  background-image: url(../img/smolov-back-desktop@1x.png);
  background-repeat: no-repeat;
  background-position: 150px 70px;
}

.companions__container {
  width: 270px;
  margin: 0 auto;
}

.companions__header {
  margin-bottom: 60px;
  padding: 0 25px;
  box-sizing: border-box;
}

.companions__title {
  font-size: 24px;
  line-height: 24px;
  color: #ff8d30;

  margin: 0;
  margin-bottom: 16px;
}

.companions__description {
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;

  width: 220px;
  margin: 0;
  margin-bottom: 20px;
  color: #ffffff;

  &--bold {
    font-weight: bold;
  }
}

.companions__button {
  position: relative;
}

.companions__button svg {
  position: absolute;
  right: 20px;
  top: 22px;
  fill: #1d2e5b;
}

.companions__button:focus svg {
  fill: rgba(29, 46, 91, 0.3);
}

@media (min-width: @tablet-width) {
  .companions {
    padding-bottom: 90px;
    padding-top: 175px;
    margin-top: -60px;
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
  }

  .companions__container {
    width: 618px;
  }

  .companions__title {
    font-size: 45px;
    line-height: 45px;

    padding-bottom: 60px;
  }

  .companions__description {
    font-size: 28px;
    line-height: 33px;

    width: 425px;
    margin-bottom: 40px;

    &--bold {
      font-weight: bold;
    }
  }

  .companions__button svg {
    right: 35px;
    top: 32px;
  }
}

@media (min-width: @desktop-width) {
  .companions__container {
    width: 1440px;
  }

  .companions__container--flex {
    display: flex;
    justify-content: flex-start;
    padding-left: 130px;
  }

  .companions__header {
    padding: 0;
    margin-right: 195px;
  }

  .companions__button {
    width: 274px;

    margin-top: -120px;
    margin-left: 130px;
    margin-bottom: 50px;
    position: relative;
  }
}
