.title__description {
  font-size: 36px;
  line-height: 40px;
  font-weight: 700;
  text-align: center;

  color: #ffffff;

  width: 240px;
  margin: 0 auto;
  margin-bottom: 250px;
}

.title__connect {
  width: 270px;
  margin: 0 auto;
}

@media (min-width: @tablet-width) {

  .title__description {

    font-size: 60px;
    line-height: 70px;
    width: 395px;

    margin-bottom: 0;
  }
}

@media (min-width: @desktop-width) {
  .title {
    display: inline-block;

    padding-right: 570px;
  }

  .title__description {
    text-align: left;
  }
}
