.filter-country {
  background-color: #e2e5f2;
  padding-bottom: 20px;
  padding-top: 45px;
  margin-top: -25px;

  position: relative;
  z-index: 1;

  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.filter-country__container {
  width: 270px;
  margin: 0 auto;
}

.filter-country__choose-country {
  display: flex;
  flex-wrap: wrap;
}

.filter-country__svg {
  width: 15px;
  height: 10px;

  margin-right: 12px;
}

.filter-country__title {
  display: inline-block;

  font-size: 16px;
  line-height: 22px;
  text-transform: uppercase;
  font-weight: 700;

  color: #192144;

  padding-right: 30px;
}

.filter-country__list {
  display: block;
  order: 4;
  margin: 0;
  margin-left: 25px;
  margin-top: 37px;
  padding: 0;
  list-style: none;
}

.filter-country__link {
  font-size: 16px;
  line-height: 22px;
  text-transform: uppercase;
  font-weight: bold;
  color: rgba(25, 33, 68, 0.3);

  &--active {
    color: #192144;
  }

  &:hover {
    color: rgba(25, 33, 68, 0.6);
  }

  &:active {
    color: #192144;
  }
}

.filter-country__show-country-text {
  display: none;
}

.filter-country__show-country {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 3px;
  height: 3px;
  background-color: #192144;
  border-radius: 50%;

  &:after {
    position: absolute;
    right: 6px;

    content: "";

    width: 3px;
    height: 3px;
    background-color: #192144;

    border-radius: 50%;
  }

  &:before {
    position: absolute;
    right: 12px;

    content: "";

    width: 3px;
    height: 3px;
    background-color: #192144;

    border-radius: 50%;
  }
}

@media (min-width: @tablet-width) {
  .filter-country {
    padding-top: 70px;
    padding-bottom: 50px;

    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
  }

  .filter-country__container {
    width: 620px;
  }

  .filter-country__svg-title {
    display: none;
  }

  .filter-country__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    order: 1;
    width: 370px;
    margin: 0;
    margin-right: 78px;
  }

  .filter-country__link {
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
    font-weight: 700;

    color: rgba(25, 33, 68, 0.3);

    &--active {
      color: #192144;
    }
  }

  .filter-country__show-country-link {
    font-size: 20px;
    order: 2;
  }

  .filter-country__show-country-text {
    display: inline-block;
    padding-right: 30px;

    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
    font-weight: 700;

    color: #192144;
  }

  .filter-country__show-country {
    width: 5px;
    height: 5px;

    &:before {
      width: 5px;
      height: 5px;

      right: 18px;
    }

    &:after {
      width: 5px;
      height: 5px;

      right: 9px;
    }
  }
}

@media (min-width: @desktop-width) {

  .filter-country__container {
    width: 1240px;
  }

  .filter-country__svg-title {
    display: inline-block;
  }

  .filter-country__title {
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
    font-weight: 700;

    color: #192144;
  }

  .filter-country__svg {
    width: 23px;
    height: 15px;
  }

  .filter-country__list {
    display: inline-flex;
    margin-right: 350px;
  }

  .filter-country__item {
    font-size: 20px;
  }
}


