.page-footer-main__container {
  background-color: #ffd74b;

  padding-top: 95px;
  margin-top: -70px;
  padding-bottom: 35px;

  position: relative;
  z-index: 2;

  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.page-footer-main__address {
  text-align: center;
  vertical-align: middle;

  width: 270px;
  margin: 0 auto;
}

.page-footer-main__text {
  display: inline-block;
  font-size: 16px;
  line-height: 22px;
  text-transform: uppercase;
  text-align: left;
  font-weight: bold;
  color: #192144;

  width: 145px;
  margin-bottom: 32px;

  position: relative;

  & svg {
    fill: #ffd74b;
    width: 14px;
    height: 22px;
  }
}

.page-footer-main__text-svg  {
  display: flex;
  justify-content: center;
  align-items: center;

  position: absolute;

  left: -63px;
  top: -3px;

  width: 45px;
  height: 45px;
  background-color: #121936;
  border-radius: 50%;
}

.page-footer-main__button {
  display: block;

  font-size: 18px;
  line-height: 62px;
  font-weight: bold;
  color: #1d2e5b;

  border: 3px solid #192144;
  border-radius: 10px;

  padding-left: 21px;
  padding-right: 77px;
  box-sizing: border-box;

  position: relative;

  &:hover {
    color: #ffffff;
    background-color: #192144;
  }

  &:focus {
    color: #5e647c;
  }
}

.page-footer-main__button:hover svg {
  fill: #ffffff;
}

.page-footer-main__button:focus svg {
  fill: #5e647c;
}

.page-footer-main__button svg {
  position: absolute;
  right: 20px;
  top: 23px;
  fill: #1d2e5b;
}

.page-footer-main__map {
  position: relative;
  z-index: 0;
  width: 320px;
  margin: 0 auto;
  margin-top: -35px;
  margin-bottom: -207px;

  &::after {
    position: absolute;
    content: "";
    top: 140px;
    left: 140px;
    width: 41px;
    height: 41px;
    background-image: url(../img/map-marker.svg);
  }
}

@media (min-width: @tablet-width) {
  .page-footer-main__container {
    padding-top: 125px;
    padding-bottom: 85px;
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
  }

  .page-footer-main__address {
    width: 618px;
    margin: 0 auto;
  }

  .page-footer-main__text {
    font-size: 28px;

    width: 430px;
    margin-bottom: 58px;

    & svg {
      width: 21px;
      height: 31px;
    }
  }

  .page-footer-main__text-svg {
    width: 67px;
    height: 67px;

    left: -91px;
    top: -30px;
  }

  .page-footer-main__button {
    box-sizing: border-box;
    padding-left: 37px;
    padding-right: 67px;

    line-height: 81px;
    border-radius: 20px;

    font-size: 24px;
    text-align: left;
  }

  .page-footer-main__button svg {
    right: 36px;
    top: 30px;
  }

  .page-footer-main__map {
    margin-bottom: -130px;
    width: 100%;

    &::after {
      top: 280px;
      left: 600px
    }
  }

  .page-footer-main__map-image {
    min-width: 768px;
    min-height: 500px;

    margin: 0 auto;
    display: block;
  }

}

@media (min-width: @desktop-width) {
  .page-footer-main__address {
    display: flex;
    width: 900px;
    justify-content: space-between;
  }

  .page-footer-main__text {
    margin: 0;
    align-self: center;
  }

  .page-footer-main__map {
    width: 1440px;

    &::after {
      top: 350px;
      left: 710px
    }
  }

  .page-footer-main__map-image {
    width: 100%;
  }
}
