.interest {
  position: relative;
  z-index: 3;
  padding-top: 140px;
  padding-bottom: 40px;
  margin-top: -70px;
  background-color: #192144;
  color: #ffffff;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.interest__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 270px;
  margin: 0 auto;
}

.interest__title {
  margin-bottom: 20px;
  font-size: 24px;
  line-height: 24px;
  margin: 0;
  color: #ff8d30;
}

.interest__text {
  margin: 0;
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  text-align: center;
}

.interest__login {
  text-decoration: underline;
  color: #ffd74b;

  &:hover {
    color: #ff8d30
  }

  &:focus {
    opacity: 0.3;
  }
}

.interest__mail-form {
  position: relative;

  &:hover .interest__your-mail::-webkit-input-placeholder {
    color: rgba(68, 68, 68, 0.5);
  }
}

.interest__mail-form:hover .interest__your-mail::-webkit-input-placeholder {
  color: rgba(68, 68, 68, 0.5);
}

.interest__mail-form input {
  width: 270px;
  padding-top: 18px;
  padding-bottom: 18px;
  padding-left: 24px;
  margin-bottom: 26px;
  box-sizing: border-box;
  border-radius: 10px;
  border: none;

  &:hover {
    background-color: #edeff6;
  }

  &:focus {
    background-color: #192144;
    color: #ffffff;
    border: 4px solid #ffffff;
    padding-top: 14px;
    padding-bottom: 14px;
  }
}

.interest__mail-form svg {
  width: 10px;
  height: 14px;
  fill: #192144;
}

.interest__your-mail::-webkit-input-placeholder {
  color: rgba(68, 68, 68, 0.3);
  font-weight: 700;
}

.interest__your-mail::-moz-placeholder {
  color: rgba(68, 68, 68, 0.3);
  font-weight: 700;
}

.interest__mail-deliver {
  position: absolute;
  top: 0;
  left: 216px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffd74b;
  width: 54px;
  height: 54px;
  border-radius: 10px;
}

.interest__mail-register {
  display: none;
}

.interest__text-question {
  display: block;
}

.interest__text-desktop {
  display: none;
}

.interest__phone {
  margin-bottom: 18px;
  position: relative;

  & svg {
    fill: #ffd74b;
    width: 18px;
    height: 18px;
  }
}

.interest__phone-svg {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #121936;
  width: 45px;
  height: 45px;
  border-radius: 50%;

  position: absolute;
  left: -56px;
  top: -16px;
}

.interest__mail {
  position: relative;

  & svg {
    fill: #ffd74b;
    width: 18px;
    height: 13px;
  }
}

.interest__mail-svg {
  position: absolute;
  left: -56px;
  top: -12px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #121936;
  width: 45px;
  height: 45px;
  border-radius: 50%;
}

.interest__phone:hover svg,
.interest__mail:hover svg {
  fill: #ffbd30;
}

.interest__phone:focus svg,
.interest__mail:focus svg {
  fill: rgba(255, 189, 48, 0.3);
}

.interest__phone,
.interest__mail {
  display: block;
  font-size: 16px;
  line-height: 22px;
  color: #ffd74b;
  text-transform: uppercase;

  &:hover {
    color: #ffbd30;
  }

  &:focus {
    color: rgba(255, 189, 48, 0.3);
  }
}

@media (min-width: @tablet-width) {
  .interest {
    padding-bottom: 80px;
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
  }

  .interest__container {
    width: 618px;
    margin: 0 auto;
  }

  .interest__title {
    margin-bottom: 60px;
    font-size: 45px;
    line-height: 45px;
  }

  .interest__text {
    width: 618px;
    margin-bottom: 67px;
    font-size: 28px;
    line-height: 33px;
    text-align: center;
  }

  .interest__text-question {
    display: inline;
  }

  .interest__mail-form input {
    width: 620px;
    padding-top: 24px;
    padding-bottom: 26px;
    padding-left: 35px;
    margin-bottom: 55px;
    border-radius: 15px;
    font-size: 24px;

    &:focus {
      padding-top: 22px;
      padding-bottom: 21px;
    }
  }

  .interest__mail-deliver {
    width: 82px;
    height: 77px;

    left: 538px;

    border-radius: 15px;
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
  }

  .interest__mail {
    left: -75px;

    & svg {
      width: 27px;
      height: 19px;
    }
  }

  .interest__phone {
    margin-bottom: 38px;
    left: -75px;

    & svg {
      width: 27px;
      height: 27px;
    }
  }

  .interest__mail-svg,
  .interest__phone-svg {
    width: 67px;
    height: 67px;

    left: -91px;
    top: -24px;
  }

  .interest__phone,
  .interest__mail {
    font-size: 28px;
    line-height: 28px;
  }
}

@media (min-width: @desktop-width) {
  .interest__container {
    width: 1175px;
  }

  .interest__contacts {
    display: flex;
    justify-content: space-between;

    width: 668px;
  }

  .interest__mail-form input {
    width: 1175px;
  }

  .interest__mail-deliver {
    width: 324px;
    left: 851px;

    display: flex;
    justify-content: space-between;
    box-sizing: border-box;

    padding-left: 34px;
    padding-right: 34px;
  }

  .interest__mail-register {
    display: inline-block;

    font-size: 24px;
    line-height: 24px;
    font-weight: 700;

    color: #1d2e5b;
  }

  .interest__text {
    width: 775px;
  }

  .interest__text-question--desktop-none {
    display: none;
  }

  .interest__text-desktop {
    display: inline;
  }
}

