.title-review {
  position: relative;
  background-image: url(../img/globe-back-mobile@1x.jpg);
  background-repeat: no-repeat;
  background-position-y: -100px;
  background-position-x: center;

  background-color: #161c36;

  padding-top: 50px;

  &::after {
    position: absolute;
    content: "";
    top: 180px;
    left: 35%;
    width: 192px;
    height: 255px;
    background-image: url(../img/traveller-illustration-mobile@1x.png);
    background-repeat: no-repeat;
    background-position: 0 0;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url(../img/traveller-illustration-mobile@2x.png);
    }
  }

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url(../img/globe-back-mobile@2x.jpg);
  }
}

@media (min-width: @tablet-width) {
  .title-review {
    background-image: url(../img/globe-back-tablet@1x.jpg);
    background-position: right -400px top 0;
    background-position-y: 290px;

    background-color: #182044;

    position: relative;
    z-index: 1;
    margin-bottom: -231px;

    padding-top: 80px;

    &::after {
      top: 315px;
      left: 45%;
      width: 372px;
      height: 451px;
      background-image: url(../img/traveller-illustration-tablet@1x.png);

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background-image: url(../img/traveller-illustration-tablet@2x.png)
      }
    }

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url(../img/globe-back-tablet@2x.jpg);
    }
  }
}

@media (min-width: @desktop-width) {
  .title-review {
    background-image: url(../img/globe-back-desktop@1x.jpg);
    background-position: center;
    background-position-y: -80px;

    background-color: #161c36;

    padding-bottom: 335px;
    padding-top: 118px;

    position: relative;
    z-index: 10;

    margin-bottom: -230px;

    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;

    &::after {
      top: 23px;
      left: 40%;
      width: 431px;
      height: 640px;
      background-image: url(../img/traveller-illustration-desktop@1x.png);
    }

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url(../img/globe-back-desktop@2x.jpg);
    }
  }

  .title-review__container {
    display: flex;
    width: 1230px;
    margin: 0 auto;
  }
}

