.page-header {
  position: relative;
  z-index: 2;
  background-color: #161c36;
  padding-top: 27px;
  /* border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px; */
}

.page-header__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 270px;
  margin: 0 auto;
}

.page-header__logo {
  display: inline-block;
}

.page-header__logo-image {
  width: 100px;
  height: 19px;
}

.page-header__connect {
  display: none;
}

.page-header__toggle {
  content: "";
  position: absolute;
  top: 0px;
  right: 0;
  z-index: 1;
  display: block;
  width: 25px;
  height: 20px;
  cursor: pointer;
  border: none;
  background-color: #161c36;
}

.page-header__toggle::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 0px;
  width: 25px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff;
  border-radius: 10px;
}

.page-header__toggle--closed {
  width: 24px;
  height: 24px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 8px;
    right: 0;
    width: 24px;
    height: 2px;
    background-color: #cccccc;
    border-radius: 10px;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.page-header__name {
  display: block;
  width: 270px;
  margin: 0 auto;
  padding-top: 60px;
  padding-bottom: 10px;
  font-size: 36px;
  font-weight: 900;
  line-height: 40px;
  color: #ffffff;
}

.page-header__name--companions {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 107px;
    left: 0;
    width: 173px;
    height: 5px;
    background-color: #ff8d30;
    border-radius: 20px;
  }
}

.page-header__name--direction {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 107px;
    left: 0px;
    width: 215px;
    height: 5px;
    background-color: #ff8d30;
    border-radius: 20px;
  }
}

@media (min-width: @tablet-width) {
  .page-header {
    background-color: #182044;
  }

  .page-header__wrapper {
    width: 688px;
  }

  .page-header__logo-image {
    width: 164px;
    height: 42px;
  }

  .page-header__connect {
    display: flex;
  }

  .page-header__toggle {
    top: 12px;
    left: 330px;
  }

  .page-header__name {
    width: 688px;
    padding-top: 140px;
    padding-bottom: 65px;
    padding-left: 77px;
    font-size: 60px;
    line-height: 1.17;
  }

  .page-header__name--companions {
    &::after {
      top: 271px;
      left: 77px;
      width: 287px;
      height: 8px;
    }
  }

  .page-header__name--direction {
    &::after {
      top: 271px;
      left: 77px;
      width: 355px;
      height: 8px;
    }
  }
}

@media (min-width: @desktop-width) {
  .page-header {
    background-color: #161c34;
    padding-bottom: 0;
  }

  .page-header__container {
    display: flex;
    width: 1320px;
    margin: 0 auto;
  }

  .page-header__wrapper {
    width: 200px;
    margin: 0;
  }

  .page-header__logo-image {
    width: 200px;
    height: 52px;
  }

  .page-header__name {
    width: 1320px;
    padding-left: 129px;
    padding-top: 126px;
  }

  .page-header__name--companions {
    &::after {
      top: 257px;
      left: 128px;
      width: 287px;
      height: 8px;
    }
  }

  .page-header__name--direction {
    &::after {
      top: 257px;
      left: 128px;
    }
  }

  .page-header__toggle {
    display: none;
  }
}

