.about {
  position: relative;
  padding-top: 30px;
  padding-bottom: 80px;
  z-index: 7;
  background-color: #192144;
  background-image: url(../img/about-mobile@1x.png);
  background-repeat: no-repeat;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  background-position: center;
  color: #ffffff;

  /* &::before {
    position: absolute;
    content: "";
    z-index: 0;
    top: 0;
    left: 40%;
    width: 113px;
    height: 132px;
    border-radius: 20px;
    background-color: #141b3b;
    background-position-x: 50%;
  } */
}

.about__container {
  width: 320px;
  margin: 0 auto;
  box-sizing: border-box;
  padding-left: 50px;
}

.about__title {
  color: #ff8d30;

  margin: 0;
  margin-bottom: 18px;
}

.about__text {
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  margin: 0;

  width: 210px;

  &--bold {
    display: inline-block;
    padding-top: 18px;
    font-weight: bold;
  }
}

.about__button {
  display: none;
}

@media (min-width: @tablet-width) {

  .about {
    padding-top: 140px;
    padding-bottom: 85px;
    background-image: url(../img/about-tablet@1x.png);
    background-position: right top 140px;
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
  }

  .about__container {
    width: 768px;
    padding-left: 75px;
  }

  .about__title {
    font-size: 45px;
    line-height: 45px;

    margin-bottom: 48px;
  }

  .about__text {
    font-size: 28px;
    line-height: 33px;

    width: 300px;
    margin-bottom: 50px;

    &--bold {
      font-size: 21px;
      line-height: 25px;
      padding-top: 38px;
    }
  }

  .about__button {
    display: inline-block;

    color: #1d2e5b;
    background-color: #ffd74b;
    border-radius: 15px;

    font-size: 24px;
    line-height: 24px;
    font-weight: 700;

    box-sizing: border-box;
    padding: 26px 125px 26px 35px;
    position: relative;

    &:hover {
      background-color: #ffbd30;
    }

    &:focus {
      color: rgba(29, 46, 91, 0.3);
    }
  }

  .about__button:focus svg {
    fill:rgba(29, 46, 91, 0.3);
  }

  .about__button svg {
    position: absolute;
    right: 36px;
    top: 30px;
    fill: #1d2e5b;
  }
}



@media (min-width: @desktop-width) {
  .about {
    background-image: url(../img/about-desktop@1x.png);
    background-position: right top 189px;

    border-bottom-left-radius: 40px;
    border-bottom-right-radius: 40px;

   /*  padding-left: 130px; */
    padding-top: 235px;
    padding-bottom: 163px;
  }

  .about__container {
    width: 1440px;
    padding-left: 130px;
  }

  .about__title {
    margin-bottom: 65px;
  }

  .about__text {
    width: 612px;
    margin-bottom: 80px;

    &--bold {
      width: 410px;
    }
  }
}
