.profile-details {
  padding-top: 23px;
  padding-bottom: 21px;
  background-color: #edeff6;
}

.profile-details__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 270px;
  margin: 0 auto;
  margin-bottom: 33px;
}

.profile-details__avatar {
  margin: 0;
  margin-bottom: 19px;
}

.profile-details__avatar-image {
  display: inline-block;
  border-radius: 50%;
}

.profile-details__change-photo {
  display: inline-block;
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  font-size: 17px;
  line-height: 30px;
  font-weight: 700;
  color: #161c35;
  text-transform: uppercase;
  padding-top: 5px;
  padding-bottom: 4px;
  background-color: #dfe3f0;
  border-radius: 30px;

  &:hover {
    background-color: #d1d7e9;
  }
  &:focus {
    color: rgba(29, 46, 91, 0.3);
  }
}

@media (min-width: @tablet-width) {
  .profile-details {
    padding-top: 64px;
  }

  .profile-details__container {
    justify-content: space-between;
    width: 618px;
  }

  .profile-details__avatar-image {
    border-radius: 10px;
  }

  .profile-details__change-photo {
    font-size: 17px;
    font-weight: 500;

    padding-top: 10px;
    padding-bottom: 11px;

    border-radius: 10px;
    letter-spacing: 2px;
  }
}

@media (min-width: @desktop-width) {
  .profile-details {
    z-index: 2;
  }

  .profile-details__container {
    position: relative;
    justify-content: space-between;
    align-items: center;
    width: 330px;
    margin-right: 260px;
    margin-top: -254px;
  }

  .profile-details__avatar {
    margin: 0;
  }

  .profile-details__avatar-image {
    width: 190px;
    height: 190px;

    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  .profile-details__change-photo {
    width: 190px;
    margin-left: 140px;
    margin-top: -5px;
    border-top-right-radius: 0;
    border-top-left-radius: 0;
  }
}
