.modal-country {
  z-index: 15;
  bottom: 300px;
  left: 50%;
  margin-left: -110px;
  padding-top: 30px;
  background-color: #ffffff;
  box-shadow: 0 0 30px rgba(0,0,0,0.2);
}

.modal-country-show {
  display: block;
}

.modal-country__container {
  width: 220px;
}

.modal-country__table {
  border-collapse: collapse;
  box-sizing: border-box;
}

.modal-country__letter {
  width: 41px;
  height: 41px;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  color: #1d2e5b;
  border: 1px solid #cbced9;
}

.modal-country__list {
  margin: 0;
  padding: 0;
  padding-left: 20px;
  padding-top: 18px;
  padding-bottom: 15px;
  list-style: none;
  border-left: 1px solid #cbced9;
  border-right: 1px solid #cbced9;
  border-bottom: 1px solid #cbced9;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.modal-country__item {
  font-size: 16px;
  line-height: 22px;
  color: #444444;
}

@media (min-width: @tablet-width) {
  .modal-country {
    margin-left: -266px;
    padding-left: 27px;
    padding-bottom: 96px;
    box-sizing: border-box;

    &::after {
      position: absolute;
      content: "";
      top: 0;
      left: 328px;
      width: 1px;
      height: 100%;
      background-color: #eaecf2;
    }
  }

  .modal-country__container {
    display: flex;
    width: 575px;
  }

  .modal-country__table {
    margin-right: 53px;
  }

  .modal-country__letter {
    width: 52px;
    height: 70px;
    font-size: 20px;
    letter-spacing: 2px;
  }

  .modal-country__list {
    padding: 0;
    border: none;
  }

  .modal-country__item {
    font-size: 20px;
    line-height: 30px;
  }
}

@media (min-width: @desktop-width) {
  .modal-country {
    margin-left: -500px;
    padding-bottom: 34px;
    padding-left: 27px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;

    &::after {
      left: 450px;
    }
  }

  .modal-country__container {
    width: 730px;
  }

  .modal-country__table {
    border-collapse: separate;
    border: none;
    border-spacing: 15px;
  }

  .modal-country__letter {
    width: 60px;
    height: 70px;
    border-radius: 4px;
  }
}
