.profile-transport {
  padding-bottom: 15px;
  text-align: center;
  border: 1px solid #cbced9;
  border-radius: 10px;
}

.profile-transport__wrapper {
  margin-top: -8px;
}

.profile-transport__title {
  display: inline-block;
  margin-bottom: 6px;
  padding-left: 17px;
  padding-right: 17px;
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
  text-transform: uppercase;
  color: #1d2e5b;
  background-color: #edeff6;
}

.profile-transport__container {
  display: flex;
  justify-content: center;
}

.profile-transport__container-svg {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  width: 33px;
  height: 33px;
  border-radius: 50%;
}

.profile-transport__svg {

  &--on {
    fill: #1d2e5b;
  }

  &--off {
    fill: #d9d9d9;
  }
}

@media (min-width: @tablet-width) {
  .profile-transport {
    position: absolute;
    bottom: 200px;
    left: 170px;
    width: 308px;
  }

  .profile-transport__wrapper {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-bottom: -25px;
  }

  .profile-transport__title {
    order: 2;
    width: 130px;
    margin: 0 auto;
    padding-top: 17px;
    box-sizing: border-box;
  }

  .profile-transport__container-svg {
    margin-top: 25px;
  }
}

@media (min-width: @desktop-width) {
  .profile-transport {
    position: relative;
    bottom: 0;
    left: -68px;
    margin-bottom: 20px;
  }
}
